/**
 * @version 1.0.0
 * @author trage
 * @description  100%img
 * @param src 
 * @param alt 
 * @param loadingSrc 
 */

import React, { Component } from 'react';

class LazyLoad extends Component {
  state = {
    load: false,
  }
  componentDidMount () {
    let img = new Image();
    if(this.props.src) {
      img.src = this.props.src;
      img.onload = () => {
        this.setState({
          load: true
        })
      }
    }
  }
  shouldComponentUpdate (nextProps, nextState) {
    let propsChange = JSON.stringify(this.props) !== JSON.stringify(nextProps)
    let img = new Image();
    if(nextProps.src && propsChange) {
      img.src = nextProps.src;
      img.onload = () => {
        this.setState({
          load: true
        })
      }
    }
    return true
  }
  render () {
    let {src, alt} = this.props;
    return (
      <>
        {
          this.state.load ? 
          <img style={{height: "100%", width: "100%"}} alt={alt} src={src} ></img> : 
          <img style={{height: "100%", width: "100%"}} alt="" src={this.props.loadingSrc}></img> 
        }
      </>
    )
  }
  
}
export default LazyLoad;

